import React from "react";
import ReactFC from "react-fusioncharts";
import FusionCharts from "fusioncharts";
import chart from "fusioncharts/fusioncharts.charts";
import FusionTheme from "fusioncharts/themes/fusioncharts.theme.fusion";

ReactFC.fcRoot(FusionCharts, chart, FusionTheme);

const ChartComponent = ({ data }) => {
    const { Administered_Dose1_Recip, Administered_Dose2_Recip } = data;
    const chartConfigs = {
        type: "pie3d", // The chart type
        width: "100%", // Width of the chart
        height: "400", // Height of the chart
        dataFormat: "json", // Data type
        dataSource: {
            // Chart Configuration
            chart: {
                caption: 'U.S. Covid-19 Vaccine Initiated vs Fully Immunized for Total Population',
                baseFont: "Arial",
                captionFontColor: "#005a96",
                captionFontSize: '16px',
                theme: 'fusion',
                formatNumberScale: '1',
                palettecolors: "#cd5257,#338dc9",
                useEllipsesWhenOverflow: '1',
                showPercentValues: '0',
                pieRadius: '45%',
                showValues: "1",
                labelFontColor: '#66aad7',
                labelFontBold: 'true',
                valueFontColor: "#c1272d",
                showToolTip: '0',
                startingAngle: '340',
            },
            // Chart Data
            data: [
                {
                    label: '1st Dose Completed',
                    value: Administered_Dose1_Recip
                },
                {
                    label: '2nd Dose Completed',
                    value: Administered_Dose2_Recip
                },
            ]
        }
    };
    return <ReactFC {...chartConfigs} />
}

export default ChartComponent;