import React from 'react';
import Header from './Header'
import Info from './Info'
import loaderImg from './preloader.gif'
import { AppContext } from './context'

function App() {
  const { isLoading } = React.useContext(AppContext);

  if (isLoading) {
    return <main>
      <Header />
      <img src={loaderImg} className="loading-img" alt="loading" />
    </main>
  }
  return (
    <main>
      <Header />
      <Info />
    </main>
  );
}

export default App;
