import React, { useState, useEffect } from 'react';
import axios from 'axios';
import data from './data.js';

const AppContext = React.createContext();

//const url = 'https://covid.cdc.gov/covid-data-tracker/COVIDData/getAjaxData?id=vaccination_data'

const AppProvider = ({ children }) => {
    const [info, setInfo] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    let [query, setQuery] = useState('United States');
    const [error, setError] = useState({ show: false, msg: '' });

    const toggleError = (show, msg) => {
        setError({ show, msg });
    }

    useEffect(() => {
        // console.log(data);
        fetchData();
    }, []);

    const fetchData = () => {



        if (query.length === 2) {
            query = query.toUpperCase();
        }
        else if (query.indexOf(' ') >= 0) {
            let str = query.split(' ');
            str = str.map((item) => {
                return item.charAt(0).toUpperCase() + item.slice(1)
            });
            query = str.join(' ');
        } else {
            query = query.charAt(0).toUpperCase() + query.slice(1)
        }
        const checkLongName = data.find((item) => item.LongName === query);
        const checkLocation = data.find((item) => item.Location === query);

        if (checkLongName || checkLocation) {
            data.map((item) => {
                if (item.LongName === query || item.Location === query) {
                    setInfo(item)
                    toggleError(false, '')
                }
            })
        } else {
            toggleError(true, "state doesn't exist!")
        }
        setIsLoading(false)
        setQuery('')
    }

    return (
        <AppContext.Provider value={{ isLoading, setIsLoading, info, setInfo, query, setQuery, fetchData, error, toggleError }}>{children}</AppContext.Provider>
    )
}
export { AppContext, AppProvider }