import React from 'react';
import styled from 'styled-components';
import logo from './logo.png'
import { FaSearch } from 'react-icons/fa';
import { AppContext } from './context'

const Header = () => {
    const { setQuery, fetchData, error, toggleError } = React.useContext(AppContext);
    let { query } = React.useContext(AppContext);
    const handleSubmit = (e) => {
        e.preventDefault();
        if (query) {
            fetchData();
        } else {
            toggleError(true, 'please enter valid state name');
        }
    }
    return (
        <Wrapper>
            <header>
                <div className="logo-container">
                    <img src={logo} alt="logo" className="logo" />
                </div>
                <section className="search">
                    <form className="search-form" onSubmit={handleSubmit}>
                        <input type="text" className="form-input"
                            placeholder="e.g. Washington or WA" value={query}
                            onChange={(e) => setQuery(e.target.value)} />
                        <button type="submit" className="submit-btn"><FaSearch /></button>
                        {error.show && (
                            <ErrorWrapper>
                                <p>{error.msg}</p>
                            </ErrorWrapper>
                        )}
                    </form>
                </section>
            </header>
        </Wrapper>
    )
}
const Wrapper = styled.section`
  background : var(--clr-white);
  padding : 2rem;
  header{
    display: flex;
    justify-content: space-around;
    align-items:center;
  }
  @media screen and (max-width:600px){
      header{
          flex-direction:column;
      }
  }
  .search-form{
      position : relative;
  }
  `;
const ErrorWrapper = styled.article`
position : absolute;
top : 120%;
left : 30%;
text-transform : capitalize;
font-weight : 700;
p{
    color : var(--clr-secondary-1);
    letter-spacing: var(--spacing);
}
`;
export default Header;